header{
    em{
        display: inline-block;
        position: relative;
        height: 45px;
        padding-left: 22px;

        &::after{
            position: absolute;
            top:0;
            left:0;
            bottom: 0;
            content: '';
            width: 20px;
            opacity: .4;
            background-size: auto 100%;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url(data:image/png;base64,);
        }
    }
}

.circle-tag{
    margin: 0 -10px;
}