body{max-width:768px;margin:0 auto;}
$green: #7cc521;

a{color:#333;}
.section{
    margin-bottom:10px;overflow:hidden;
    .sbj{height:50px;line-height:30px;margin-bottom:10px;padding-top:10px;padding-right:8px;overflow:hidden;border-bottom:1px solid #f7f7f7;font-size:16px;color: $green;}
    .sbj::before{display:block;content:'';width:8px;height:30px;float:left;margin-right:10px;background: $green;}
    .tab-cont{
        padding-right: 8px;overflow: hidden;
        a{position:relative;width:33.333%;height:43px;line-height:35px;padding:0 5px;float:left;font-size: 13px;border-left:8px solid white;border-bottom:8px solid white;text-align:center;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;}
        a::before{position:absolute;top:0;left:0;right:0;bottom:0;content:'';border:1px solid #eee;} 
    }
}
