li{list-style-type: none;}
input,
select,
option,
textarea{outline: none;margin:0;}
input{border:none;}
select,
option{appearance:none;-webkit-appearance:none;-moz-appearance:none;border:none;}
.clearfix{display:block;*zoom:1;}
.clearfix:after{content:'.';display:block;height:0;clear:both;visibility:hidden;}

@for $i from 1 through 4 {
	.icon-date-#{$i}{background: url(images/icon-date-#{$i}.png) center no-repeat;background-size:25px;}
}

%green-title{
	h2{position: relative;padding: 5px 10px 5px;padding-left:25px;overflow: hidden;border-bottom:1px #ddd solid;font-size: 16px;color: $green;
		&:after {content: "";display: block;position: absolute;left:12px;top: 10px;width:4px;height: 20px;border-radius: 0 3px 3px 0;background-color: $green;}
	}
}
.icon-sign{background-image: url(images/icon-sign.png);}
.icon-phone{background-image: url(images/icon-phone.png);}
.icon-chat{background-image: url(images/icon-chat.png);}
.arrow-down-gery{background-image: url(images/arrow-down-grey.png);}

.intro-nav {padding: 0 10px 10px;overflow: hidden;background:url(images/bg-intro.png) center no-repeat;background-size: cover;
	ul{height:44px;border-bottom:1px solid $green;overflow: hidden;text-align:center;background-color: #fff;}
	li{float: left;padding: 0 10px;width: 16.66%;
		a{display: block;height:43px;line-height: 43px;}
	}
	.card {padding: 12.5px;background-color: rgba(255,255,255, .9);
		dt{float: left;margin-right: 12.5px;width: 75px;height: 75px;overflow: hidden;border-radius: 50%;}
		dd{overflow: hidden;
			b{font-size: 16px;font-weight:normal;color: #000;}
			p{font-size: 13px;color: #666;word-spacing: 8px;}
			a{display: inline-block;width: 55px;height: 30px;line-height: 30px;margin-top:10px;margin-right: 5px;border-radius: 5px;text-align: center;color: #fff;background:linear-gradient(#ffbf2f,#ffb132);}
		}
	}
	.active {border-bottom: 3px solid $green;}
}

.list-banner{margin-bottom: 10px;padding:10px;padding-top:50px;background:url(images/list-banner.jpg) top center no-repeat;background-size:100%;background-color:#fff;
	.head {margin: 0 auto;padding-top:10px;width: 250px;height:70px;overflow: hidden;text-align:center;border-radius:5px;background-color:#fff;
		h2{font-size: 18px;}
		p {font-size: 9px;color: #999;}
	}
	.words{line-height: 2;font-size: 9px;color: #666;background-color:#fff;}
	
}

.date-tab{margin-bottom: 13px;background-color: #fff;
	a{display: block;}
	@extend %green-title;
	dl{text-align: center;font-size: 12px;}
	dt,dd{float:left;margin-top: -1px;margin-left: -1px;width:25px;height: 35px;line-height:35px;overflow: hidden;border:1px $green solid;width: 12.5%;}
	dt{width: 55px;}
	table {margin:0 auto 10px;width: 95%;}
	caption{margin: 10px 0;text-align: left;font-size: 14px;
		a{color: #000;}
	}
	td{text-align: center;border-color: $green;}
	ul{margin-bottom: 5px;padding-left: 15px;overflow: hidden;}
	li{float:left;padding-left:28px;width:25%;background-position:left center;}
	p{margin-bottom:15px;padding-left:15px;font-size: 14px;color: #999;}
	.address{ padding: 15px;overflow: hidden;border-top: 1px solid #ddd;
		li{float: none;padding: 0;width: 100%;font-size: 14px;}
	}
		em{color: $green;}
		b{font-weight:normal;color: #666;}
	.whole-link {border-top: 1px solid #ddd;}
}

.date-answer {margin-bottom: 13px;padding:10px 0;background-color: #fff;
	@extend %green-title;
	h2{margin-bottom:0}
	dl{margin-top:-1px;padding: 10px; border-top: 1px solid #ddd;
		a{color: $green;}
	}
	b{float: left;margin-right: 5px;font-weight: normal;}
	p{overflow: hidden;color: #666;  display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 4;display: -moz-box;-moz-box-orient: vertical;-moz-line-clamp: 4;display: -o-box;-o-box-orient: vertical;-o-line-clamp: 4;display: -ms-box;-ms-box-orient: vertical;-ms-line-clamp: 4;display:box;box-orient: vertical;line-clamp: 4;}
	dt{overflow: hidden;
		a{color:#333;}
		b{color: $green;}
		p{font-size: 13px;font-weight:bold;color: #000;}
	}
	dd{ overflow: hidden;
		b{color: #ffbd73;}
		p{font-size: 11px;color: #666;}
	}
	ul{padding: 10px;min-width: 300px;
		li{float: left;width:50%;height: 40px;line-height:40px;overflow: hidden;text-align:center;
			a{display: block;height:40px;border-radius:8px;color: #fff;}
		}
		li:nth-child(1){padding-right: 5px;}
		li:nth-child(1) a{background:linear-gradient(#97d86e,#79c84b);}
		li:nth-child(2) {padding-left: 5px;}
		li:nth-child(2) a{background:linear-gradient(#ffc62d,#ffb132);}
	}
}

.doctors-search {
	.search {margin-bottom: 12px;padding-top: 7px;padding-left:7px;width: 100%;overflow: hidden;
	input:nth-child(1){float: left;padding-left:28px;width: 90%;height:30px;border:1px solid #ddd;background: url(images/icon-search.png) 7px center no-repeat;background-size:14px;background-color: #fff;
		&{text-shadow:0 0 0 #000;-webkit-text-fill-color: transparent;color:blue;}
		&::-webkit-input-placeholder{text-shadow:none;-webkit-text-fill-color:initial;color:#999;}
		&::-moz-placeholder{text-shadow:none;-webkit-text-fill-color:initial;color:#999;}
	}
	input:nth-child(2){float: left;width:10%;height:30px;text-align: center;color:#000;background-color:#f2f2f2;}
	}
	.hot{padding: 12px 15px;overflow: hidden;border-top: 1px solid #ddd;background-color: #fff;}
	h2{position:relative;margin-bottom: 25px;padding-left: 40px;font-size: 14px;font-weight: 600;}
	b{display:block;position: absolute;left:0;top:0;width:30px;height:30px;line-height: 30px;overflow:hidden;text-align:center;font-size: 11px;font-weight:normal;color: #fff;background: url(images/icon-flag.png) center no-repeat;background-size: 100%;}
	li{float: left; padding: 5px;width: 25%;
		a{display:block;width:100%;height:35px;line-height:35px;box-shadow:0 0 2px #ddd;overflow:hidden;text-align:center;color:$green;}
	}
}

.search-failed {margin-bottom:10px;padding:10px 0 10px 10px;color:#999;background-color:#fff;}

.doctors-intro{margin-bottom:10px;padding-bottom:10px;overflow:hidden;background-color: #fff;
	@extend %green-title;
	.intro{padding-top: 10px;text-indent: 2em;}
	h2{a{color:$green;}}
	p{margin-bottom: 10px;padding: 0px 15px 0;}
	.words{margin:0;margin-bottom: 7px;padding-top: 5px;padding-bottom: 5px;overflow:hidden;;}
	.more-load{display: block;position: relative;margin-bottom: 5px;text-align:center;color: $green;
		.icon-down {padding-right:20px;cursor: pointer;background: url(images/arrow-down.png) right center no-repeat;background-size:10px 5px;}
		.icon-up {@extend .icon-down;background-image: url(images/arrow-up.png);}
		.icon-open{@extend .icon-down;}
	}
	.tag{padding-left: 15px;padding-top: 5px;border-top: 1px solid #ddd;
		a{margin-right: 7px;color:#666;&:last-child{color:$green;}}
		em{color: $green;}
		p{height:25px;padding-left:0;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;color: #999;}
	}
	dt{position:relative;float: left;margin-left:15px;width:30px;height:30px;overflow:hidden;background-size:30px;background-repeat: no-repeat;}
	dd{padding-left:15px;line-height:30px;overflow: hidden;color: #666;background-position: left center;}
	dl:first-of-type{margin-top: -1px;padding-top:10px;border-top:1px solid #ddd;
		dt{margin-left:0px;width:60px;height:60px;overflow: hidden;border-radius:50%;img{width: 100%;}}
		dd{padding-left: 15px;
			strong{display: block;font-size:13px;font-weight: normal;color: #000;}
			b{display: block;font-size:13px;font-weight: normal;}
		}
		.line{top:78px;padding-left: 0;}
	}
	dl{position:relative;margin-bottom: 20px;padding:0 13.5px ;
		.line{position:absolute;left:43px;top:35px;padding:0;width: 1px;height:10px;background-color:#ddd;}
	}
	.whole-link {margin-top: -1px;border-top: 1px solid #ddd;}
	.news-main-info {padding: 10px 12px;
		p{padding: 0;}
		.tags{padding:0;}
		.link-list-square li {padding: 0 10px 0 15px;
			&:after {left:0;}
		}
		& + .whole-link {border:transparent;}
	}
	.noun {margin-bottom: 7px;padding:0 10px;overflow: hidden; 
		li{float:left;text-align: center;margin-bottom:10px;padding:0 5px;
			a{display: block;padding: 0 10px;width:100%;height: 40px;line-height: 40px;border: 1px solid #ddd;}
		}
	}
	.video-link{border-bottom: none;}

}
.section h2{margin-bottom:0;padding-bottom: 0;border-bottom:none;}

/*doctors images*/
.doctors-photo{margin-bottom: 10px;overflow: hidden;background-color: #fff;
	@extend %green-title;
	.whole-link{margin: 0;}
	.photo-slider {
		span{
			display: block;float: left;width: 25%;overflow: hidden;text-align:center;
			a{display:inline-block;width: 80px;height: 80px;margin:0 auto;padding:0;float: none;border-radius: 50%;overflow: hidden;}
			a:nth-child(1){padding-right: 0;}
			img{width:100%;}
		}
	}
}

.doctors-comment {@extend .doctors-intro;padding-bottom:0;background-color: transparent;
	.cont {margin-bottom: 10px;overflow: hidden;background-color: #fff;}
	.cont:nth-last-child(2){margin-bottom:0px;}
	h2,.whole-link
	{background-color: #fff;}
	h2:first-of-type{padding-top: 15px;
		&:after{top: 19px;}
	}
	span{padding-left:15px;font-size: 10px;color:#999;}
	dl:first-of-type{margin-bottom:0px;padding-top:5px;padding-bottom:5px;overflow: hidden;background-color: #fff;
		dt{margin-top: 10px;width:40px;height:40px;overflow: hidden;;border-radius:50%;img{width: 100%;}}
		dd{strong{font-size:13px;}b{font-size:12px;}}
	}
	p{margin-bottom: 15px;border-top:1px solid #ddd;}
	.whole-link{margin-top:0;cursor: pointer;}
	.whole-link:first-of-type{padding:10px 10px;width: 100%;color:#fff;
		i{display: block;width: 100%;border-radius:5px;background-color: $green;}
	}
	.pager {border-top: 1px solid #ddd;}
	.icon-open{@extend .icon-down;}
}

.doctors-article{@extend .doctors-intro;
	.article-tabs-list {padding: 0px 15px; height: 42px;line-height: 40px;
		a{float: left;width: 50%;text-align: center;font-size: 16px;}
	}
	p{padding:0;}
	.news-main-info{border-top: 1px solid #ddd;border-bottom: 0;
		.info{text-indent:0em;}
	}
	ul{overflow: hidden;}
	li{float: left;padding:0 15px;width: 50%;height: 45px;line-height: 43px;text-align: center;font-size: 16px;}
	.on{display: block;width: 100%;overflow:hidden;border-bottom: 2.5px solid $green;color:$green;}
	.pager{border-top: 1px solid #ddd;}
}

.my-comment {@extend %green-title;padding-bottom: 15px;background-color: #fff;
	ul{padding-left: 15px;overflow: hidden;}
	li{padding-right: 15px;border-bottom: 1px solid #ddd;line-height: 45px;overflow: hidden;font-size: 13px;
		em{float: left;margin-right: 10px;overflow: hidden;}
	}
	.cont{position: relative;overflow: hidden;
		input[type='text']{width: 100%;height: 45px;}
		input[type='text']::-webkit-input-placeholder{font-size: 13px;color:#999;}
		input[type='text']::-moz-placeholder{font-size: 13px;color:#999;}
		input[type='text']::-ms-input-placeholder{font-size: 13px;color:#999;}
		label{position: absolute;right: 0px;top:0;padding-left: 17px;width:60px;height:45px;}
		input[type='checkbox']{position:absolute;left:0;top:15px;margin-right: 3px;width: 15px;height: 15px;border: 1px solid #ddd;background-color: #fff;}
		strong{margin-left:3px;font-weight: normal;}
		select{padding-right: 30px;width: 100%;height: 45px;overflow: hidden;font-size:13px;direction:rtl;background-position: right center; background-repeat: no-repeat;background-size: 20px;}
		option{padding-right: 20px;color: #000;}
		textarea{margin-bottom: 10px;padding:10px 10px 0px;width: 100%;height: 140px;border: 1px solid #ddd;resize:none;}
	}
	li:first-of-type input[type='text']{padding-right: 50px;}
	li:nth-child(4) select{color: #999;}
	li:last-of-type{border-bottom: transparent;
		em{display: block;float: none;}
	}
	.whole-link{ padding: 0 15px;
		i{display:block;cursor: pointer;color:#fff;background-color: $green;}
	}  
}

