.forum-news {
	padding:0px 12.5px;
	margin-top: -15px;
	ul {padding:10px 0;border-bottom: 1px solid #ddd;}
	ul:last-of-type{border-bottom: 1px solid #ddd;}
	a{display: block;padding: 3px 0;overflow: hidden;text-overflow:ellipsis;white-space:nowrap;}
	em {margin-right: 10px;color: #666;}
}

.circle {
	.intro-nav {padding:0;margin-bottom: 15px;}
	.nav {border-bottom: 1px solid $green;
		li {padding:0;width: 20%;}
	}
	.active {border-color: $green;}
	.list{
		dl{
			overflow: hidden;
			padding:10px;
			border-bottom: 1px solid #ddd;
			dt{margin-bottom: 8px;}
			dd {
				font-size: 12px; 
				a,
				em{float: left;}
				em,
				i{color: #999;}
				a{margin-right: 10px;}
				i{float: right;padding-left:20px;background-repeat: no-repeat;background-position: left center;}
				i:nth-last-child(1){margin-right: 10px;background-image: url(images/icon-eye.jpg);}
				i:nth-last-child(2){background-image: url(images/icon-chat.jpg);}
			}
		}
	}
	.order {
		li{float: left;padding: 0 10px;width: 50%;}
		a{display: block;padding: 10px 0;border:1px solid #ddd;border-radius: 4px;text-align: center;color: #666;}
	}
}

.circle-title {
	padding: 10px;
	overflow: hidden;
	h1{float: left;margin-right: 8px;font-size: 16px;font-weight:bold;}
	p{
		line-height: 29px;
		overflow: hidden;
		font-size: 12px; 
		color: gray;
		em:nth-child(1){color: orange;}
		i{margin: 0 5px;}
	}
	span {
		font-size: 12px;
		a{color: $green;}
	}
}
  
.circle-article {
	padding: 0 10px;
	margin-top: 10px;
	h1{margin-bottom: 6px;font-size: 16px;font-weight: bold;}
	.info{
		display: block;
		margin-bottom: 10px;
		font-size: 12px;
		color: gray;
		em{margin: 0 5px 0 10px;}
	}
	b{display: block;margin-bottom: 10px;font-size: 14px;font-weight: normal;}
	.detail {
		font-size: 16px;
		p{padding: 6px 0;}
	}
}

.circle-comment {
	dl {
		padding: 10px;
		overflow: hidden;
		border-bottom: 1px dotted #ddd;

		dd{
			padding-top: 6px;
			text-align: right;
			font-size: 12px;
			color: gray; 

			em{padding-left:10px;}
		}
	}
}

.circle-news {
	.sbj {
		a {color:$green;}
		span{float: right;font-size: 12px;color:#999;
			a{color:#666;}
		}
	}
	.news {
		padding:10px;

		li{
			padding: 10px 0;
			border-bottom: 1px solid #ddd;
		}
		b{
			font-weight: normal;
			font-size: 16px;
		}
		p{
			padding-top: 5px;
			font-size: 12px;
			color: gray;
			a{
				color: $green; 
			}
		}

	}

}
